@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: montserrat, sans-serif;
}



.App {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #53565A;
}

form {
    display: block;
    position: relative;
}

form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    bottom: -5px;
    right: -5px;
    z-index: 1;
    background-image: linear-gradient(to bottom right,#c7d8e7, rgb(127,255,212));
}

form .form-inner, .register {
    position: relative;
    display: block;
    background-color: #fff;
    padding: 30px;
    z-index: 2;
}

form .form-inner h2 {
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

form .form-inner .form-group {
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

.form-inner .form-group label {
    display: block;
    color: #666;
    font-size: 12px;
    margin-bottom: 5px;
    transition: 0.4s;
}

.form-inner .form-group:focus-within label {
    color: #0d659d;
}

form .form-inner .form-group input, .register  {
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #f8f8f8;
    border-radius: 8px;
    transition: 0.4s;
}

form .form-inner .form-group input:focus, .register:focus {
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}

form .form-inner input[type="submit"],
.welcome button,.register {
    display: inline-block;
    padding: 10px 15px;
    border-radius: 8px;
    background-image: linear-gradient(to right, #6bcae2 50%, #6bcae2 50%, #0d659d);
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
}

form .form-inner input[type="submit"]:hover,
.welcome button:hover, .register:hover {
    background-position: 100% 0%;
}

.error {
    text-align: center;
    color: red;
}

.welcome {
    width: 100%;
    max-width: 480px;
    background-color: #fff;
    padding: 15px 30px;
}

.welcome h2 {
    text-align: center;
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

.welcome h2 span {
    color: #0d659d;
    font-weight: 700;
}

.register {
    position: relative;
    width: 100px;
    margin-left: 130px;
    background-image: linear-gradient(to right, #0d659d 50%, #0d659d 50%,#6bcae2);
}
 
/* Nav bar general layout */

nav {
	position: absolute;
	background-color: rgb(127,255,212);
	display: block;
	width: 100%;
	background-image: linear-gradient(to right, rgb(127,255,212), #c7d8e7);
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size: 14px;	
}
/*Make the scrolling smooth*/
html {
    scroll-behavior: smooth;
}


.topnav {
	border-bottom: 1px solid #c7d8e7;
	height: 50px;

}

.topnav a {
	top: 0;
	float: right;
	padding: 5px 5px 5px 5px;
	margin: 13px 5px 5px 5px;
	text-decoration: none;
	color: black;
	
}
.topnav a:hover{
	border-radius: 10px;
	color: #000000;
	background-color: #7fffd4;
	text-decoration: none;
}

.bottomnav {
	border-top: 1px solid #c7d8e7;
	background-image: linear-gradient(to left, rgb(127,255,212), #c7d8e7);
	height: 50px;
	position: fixed;
	left: 0;
	bottom: 0;
}
.bottomnav a {
	bottom: 0;
	float: left;
	padding: 5px 5px 5px 5px;
	margin: 13px 5px 5px 5px;
	text-decoration: none;
	color: black;
}

.bottomnav a:hover{
	border-radius: 10px;
	color: #000000;
	background-color: #7fffd4;
}
.signin a{
	border-style: outset;
	text-align: center;
	text-decoration: none;
	padding: 5px 30px 5px 30px;
	margin: 10px 5px 5px 5px;
	border-radius: 10px;
	cursor: pointer;
}
.signin a:hover {
	background-color: #7fffd4;
	box-shadow: 0 5px 5px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
	color: black;
}

.logo {
	font-family: 'Lobster', cursive;
	font-size: 20px;
	margin: 0 30px;
	padding: 11px 0;
	float: left;
}

.logo a {
	padding:0;
	margin: 0;
	color: #0d659d;
}

.logo a:hover {
	color: #000;
}

#myBtn {
	/*    smooth scroll to top instead of jump*/
	scroll-behavior: smooth;
	display: none;
	position: fixed;
	text-align: center;
	width: 60px;
	height: 20px;
	bottom: 45px;
	right: 30px;
	z-index: 99;
	font-size: 15px;
	border: ridge;
	outline: none;
	background-color: #7fffd4;
	color: #000000;
	cursor: pointer;
	padding: 5px 15px 20px 15px;
	border-radius: 10px;
	border: inset;
}
/*Username dropdown list css*/
.topnav .navbar-dropdown a {
	margin: 7px 25px 10px 75px;
	padding: 7.5px 0px 7.5px 0px;
	background-color: transparent;
}
.topnav .navbar-dropdown-elements {
	overflow: hidden;
	max-height: 0;
	position: absolute;
	right: 1%;
	transition: max-height 0.7s ease;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	padding: 35px 0px 0px;
	margin: 5px 0px 0px 0px;
	border: solid 1px rgba(0,0,0,0.2);
	border-radius: 10px;

}
.topnav .navbar-dropdown-elements a {
	display: block;
	float: none;
	text-decoration: none;
	color: black;
	padding: 10px 5px 0px;
	text-align: right;
	margin: 5px;
}

.topnav .navbar-dropdown:hover .navbar-dropdown-elements {
	max-height: 250px;
}
body {
	margin: 0px;
	width: 100%;
	background-image: linear-gradient(to right, #c7d8e7, rgb(248,248,255));
}
#content {
	margin: 0px 60px 10px 65px;
}
#content .dropdown {
	float: right;
	padding: 5px;
	border: solid 1px rgba(0,0,0,0.2);
	border-radius: 5px;

}
hr {
	width: 100%;
}
header {
	font: bold 25px verdana;
	width: 100%;
	margin: 0px 0px 15px 0px;
}
h2 {
	font: bold 15px verdana;
	float: right;
	margin: 5px 10px 0px 0px;
}
h1 {
	float: left;
	display: inline-block;
	font: bold 20px verdana;
	width: 37%;
	margin: 20px 0px 0px 0px;

}
.profile {
	height: 100px;
	display: inline-block;
	float: left;
	width: 50%;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
	border-radius:25px;
	
}
.daily-challenge {
	width: 50%;
	height: 100px;
	clear: right;
	margin-top: 10px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0, 0, 0, 0.2);
	border-radius:25px;
}
#leaderboard {
	width: 40%;
	background-color: #7fa6c9;
	color: #fdfaf5;
	min-height: 100px;
	display: inline-block;
	margin-left: 30px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0, 0, 0, 0.2);
	border-radius:25px;
	text-align: center;
	font: bold 15px verdana;
	padding-top: 5px;
	padding-bottom: 5px;
}
#leaderboard ol {
	list-style-type: none;
	margin: 10px 0px 20px 0px;
	padding: 0px;

}
#leaderboard li {
	text-align: left;
	width: 92%;
	margin: 7.5px 10px 7.5px 10px;
	padding: 10px 0px 10px 25px;
	background-color: #050b1f;
	color: #fdfaf5;
	text-decoration: none;
	font: 15px verdana;
	overflow: hidden;
	border-radius: 5px;
}

#leaderboard li:nth-child(1) {
	background-image: linear-gradient(to right, 
						gold 0%, 
						#050b1f 3%);
	color: gold;
}

#leaderboard li:nth-child(2) {
	background-image: linear-gradient(to right, 
						silver 0%, 
						#050b1f 3%);
	
	color: silver;
}
#leaderboard li:nth-child(3) {
	background-image: linear-gradient(to right, 
						#cd7f32 0%, 
						#050b1f 3%);
	
	color: #cd7f32;
}

#content .dropdown-elements {
	overflow: hidden;
	opacity: 0;
	max-height: 0;
  	z-index: 1;
	padding: 5px 50px 5px 5px;
	margin: 5px;
  	position: absolute;
	right: 3%;
	transition: opacity 0.5s ease-out, max-height 0.3s ease;
  	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
  	border: solid 1px rgba(0,0,0,0.2);
	border-radius: 10px;
}
#content .dropdown-elements a {
	display: block;
	float: none;
	text-decoration: none;
	padding: 5px;
	margin:  5px;
	color: black;
	text-align: right;
}
#content .dropdown:hover .dropdown-elements {
	opacity: 1;
	max-height: 100%;
}
#content .category {
	width: 100%;
	height: 110px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
	border-radius:25px;
	margin: 0px 0px 30px 0px;
	text-align: center;
	font: 15px verdana;
}
#content .arrow {
	display: inline-block;
	border: solid black;
	border-width: 0px 4px 4px 0px;
	padding: 5px;
	margin: 0px 0px -3px 0px;
}
#content .right {
	float: right;
	margin: 10px 10px 0px 0px;
	transform: rotate(315deg);
}
#content .left {
	float: left;
	margin: 10px 0px 0px 10px;
	transform: rotate(-225deg);
}
#content #js-category-name {
	display: inline-block;
}
#content .game {
	width: auto;
	float: left;
	height: 75px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
	border-radius:25px;
	margin: 5px 20px 10px 20px;
}
#content .continue {
	width: 100%;
	height: 100px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
	border-radius:25px;
	margin: 0px 0px 30px 0px;

}
#content .search-bar {
	float: left;
	display: inline-block;
	width: 60%;
	margin: 20px 0px 0px 0px;
}
#content .search-button {
	margin: 21.2px 0px 15px -20px;
	width: 19px;
}

